.featured{

background: #1A1A1A ;

.content{
 

    display:flex;
    flex-direction: column;
    align-items: center;
    padding-top: 5vh;
    padding-bottom: 10vh;
    justify-content: center;
   // width: 70%;
   width: 100%;

    padding-top:15vh;
    justify-content: center;

    @media (max-width: 1200px) {
        display: block;

    }

   /* @media (max-width: 1200px) and (min-width: 801px) {
        min-height: 125vh;
    }*/

        img:hover{
            filter: brightness(80%) blur(2px);
              
        }
 
    }
}




.space{
    height: 1px;
    background: white;
}



.item {
                
    text-align: left;
    justify-content: space-between;
    
    display: flex;
    width: 100%;
   
    border-bottom: 2px solid  #CEC5FC;
    margin: 10px 20px;
    padding: 2vh;
    transition: all 1s ease;
    
    .itemInfo {
        width: 900px;
        padding-right: 5vh;
        h3 {
        
            font-size: 85px;
            padding-left: 1vh;
            margin: 0;
            padding-bottom: 1vh;
            color: white;

            @media (max-width: 800px) {
                font-size: 16px;
                padding-bottom: 0;
            }
            
        }
        p{
            color: white;
            margin: 0;
            font-size: 3vh;
            font-style: italic
        }
    
    }

    .projectImage {
        height: 300px;
        width: 400px;
        img {
            width: 100%;
            height: 300px;
            object-fit:cover;
            z-index: 1;
            border-radius: 20px;
            

            @media (max-width: 1200px) and (min-width: 801px) {
                width: 100%;
                height: 135px;
            }
        }
    }
    a {


        text-decoration: none;
        font-family: 'Raleway', sans-serif; 
        :hover {
            color: #FCC5F3
        }

    }}